@keyframes scrolldownanime {
    from {
        bottom: 80px;
    }
    to{
        bottom: 10px;
    }
}

@keyframes pointanimation {
    0% {
        height: 10px;

    }
    50%{
        height: 10px;
    }
    70%{
        height: 10px;
    }
    80%{
        height: 10px;
    }
    100%{
        height: 0px;
    }
}

@keyframes scrolldownholderanime {
    from {
        align-items: flex-start;
    }
    to{
        align-items: flex-end;
    }
}

.glass{
    width: 100%;
    height: 100vh;
    position: fixed;
    backdrop-filter: blur(10px);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 0;
}

.contant{
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    background-color: #00000000;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
}

.contant::-webkit-scrollbar {
    width: 1px;
  }
  /* Track */
  .contant::-webkit-scrollbar-track {
    background: #00000000;
    width: 1px;
  }
  
  /* Handle */
  .contant::-webkit-scrollbar-thumb {
    background: #4492FF;
    width: 1px;
  }
  
  /* Handle on hover */
  .contant::-webkit-scrollbar-thumb:hover {
    background: #60a0fb;
    width: 5px;
  }
  .contant::-webkit-scrollbar:hover{
    width: 10px;
  }
.contant .headdiv{
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.contant .headdiv .scrolldownholder{
    position: absolute;
    width: fit-content;
    height: 80px;
    bottom: 0px;
    left: 50%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    /* animation-name: scrolldownholderanime; */
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
}
.contant .headdiv .scrolldownholder .scrolldown{
    position: absolute;
    width: 30px;
    height: 30px;
    bottom: 80px;
    border-style: solid;
    border-width: 1px;
    border-color: white;
    border-radius: 100px;
    animation-name: scrolldownanime;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
}
.contant .headdiv .scrolldownholder:hover .scrolldown{
    animation-play-state: paused;
    cursor: pointer;
}
.contant .headdiv .scrolldownholder:hover .scrolldown .info{
    opacity: 1;
}
.contant .headdiv .scrolldownholder .scrolldown .info{
    border-radius: 100px;
    width: 100px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    background-color: #4492FF;
    color: white;
    opacity: 0;
    cursor: pointer;
}
.contant .headdiv .scrolldownholder .scrolldown .point{
    width: 10px;
    height: 10px;
    background-color: white;
    border-radius: 100%;
    margin: 10px;
    /* animation-name: pointanimation; */
    animation-duration: 0.2s;
    animation-delay: 2s;
    /* animation-timing-function: ease; */
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
}
.contant .headdiv .item{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.contant .headdiv .item .logo {
    width: 300px;
    height: 300px;
    border-radius: 100%;
    border-style: solid;
    border-width: 2px;
    border-color: white;
    box-shadow: white 0px 0px 10px 0px;
    overflow: hidden;
    cursor: pointer;
}
.contant .headdiv .item .skilsbox{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contant .headdiv .item .skilsbox .skil {
    margin: 10px;
    width: 25%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}
.contant .headdiv .item .skilsbox .skil img{
    width: 80%;
}
.contant .headdiv .item .skilsbox .skil.js img{
    width: 50%;
}

.contant .headdiv .item .logo:hover {
    box-shadow: white 0px 0px 100px 0px;
    width: 350px;
    height: 350px;
}

.contant .headdiv .item .logo img{
    width: 100%;
    border-radius: 100%;
    cursor: pointer;
}

.contant .headdiv .item h1{
    font-size: 80px;
    color: white;
    cursor: pointer;
}

.contant .headdiv .item h1:hover{
    text-shadow: white 0px 0px 10px;
}
.typedbox {
    color: white;
}

.typedbox span{
    color: #60a0fb;
    text-shadow: #60a0fb 0px 0px 20px;
}

.skills{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.skills .item{
    width: 50%;
    height: 100vh;
}
.skills .icons{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.skills .icons .skil{
    width: 80px;
    height: fit-content;
    margin: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.skills .icons .skil img{
    width: 100%;
    cursor: pointer;
}
.skills .content{
    background-color: #222;
}
.skills .icons .skil .jsunderline{
    width: 0%;
    height: 5px;
    background-color: #ffdf00;
    border-radius: 100px;
    margin-top: 10px;
    box-shadow: #ffdf00 0px 0px 10px 0px;
}
.skills .icons .skil .nextjsunderline{
    width: 0%;
    height: 5px;
    background-color: #74d4cf;
    border-radius: 100px;
    margin-top: 10px;
    box-shadow: #74d4cf 0px 0px 10px 0px;
}
.skills .icons .skil .pyunderline{
    width: 0%;
    height: 5px;
    background: #31709f;
    background: linear-gradient(90deg, #31709f 50%, #ffde53 50%, #ffde53 50%);
    border-radius: 100px;
    margin-top: 10px;
    box-shadow: #31709f -15px 0px 15px 0px, #ffde53 15px 0px 15px 0px;;
}
.skills .icons .skil .reactunderline{
    width: 0%;
    height: 5px;
    background-color: #61dafb;
    box-shadow: #61dafb 0px 0px 10px 0px;
    border-radius: 100px;
    margin-top: 10px;
}
.skills .icons .skil .activeunderline{
    width: 100% !important;
}
.workex{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    background-color: #222;
    padding-bottom: 100px;
}
.workex .workexitem{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.workex .workexitem.postionsticky{
    position: sticky;
    padding: 20px;
    top: 0;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 100vh;
}

.workex .workexitem.postionsticky h1{
    color: #ffdf00;

}
.workex .workexitem.postionsticky p{
    color: white;
    font-weight: bolder;
    text-align: justify;
}


.skills *{
    color: white;
}


.workcart{
    width: 320px;
    height: 400px;
    background-color: #22222251;
    backdrop-filter: blur(50px);
    border-radius: 20px;
    box-shadow: #363636 0px 0px 10px 0px;
    margin: 15px;
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 10px;
}
.workcart img{
    width: 200px;
    height: 200px;
    border-radius: 15px;
    background-color: rgb(228, 220, 202);
    box-shadow: whitesmoke 0px 0px 10px 0px;   
}
.workcart p{
    text-align: center;
    color: white;
}
.workcart a{
    cursor: pointer;
}
.footer{
    width: 100%;
    height: 40vh;
    background-color: #003146;
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer .item {
    width: 50%;
    height: 100%;
}
.footer .item.leftside{
    background-color: #222;
}
.skilschart{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #003146;
    height: fit-content;
    padding-bottom: 20px;
}



.skilschart .item{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}
.skilschart .item p{
    padding: 10px;
    color: white;
    text-align: justify;
}
.skilschart .cs50icon {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.skilschart .item h1{
    color: #ffdf00;
    padding: 10px;
    text-align: justify;
}
.skilschart .cs50icon img{
    width: 80%;
    border-radius: 20px;
    cursor: pointer;
}
.skilschart .cs50icon img:hover{
    box-shadow: 0px 0px 200px 0px white , 0px 0px 150px 0px #ffdf00;
    transform: scale(1.1);
}
.footer .item .cvlinks{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row-reverse;
}
.footer .item .cvlinks a svg{
    padding: 10px;
    color: white;
    width: 40px;
    height: 40px;
    cursor: pointer;
}
.footer .item .cvsbox{
    width: 50%;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    
}
.footer .item {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 35px;
}
.footer .item h1{
    color: white;
}
.footer .item .cvlinks a{
    color: white;
    font-weight: bolder;
    text-decoration: none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer .item *{
    color: white;
}
.footer .item.leftside{
    flex-direction: column-reverse;
}
.skills .item.content .chartbox{
    width: 90%;
    height: fit-content;
}
.skills .item.content{
    display: flex;
    justify-content: center;
    align-items: center;
}
.marquee{
    background-color: #2a2a2a;
    color: white;
    padding: 10px;
    margin-bottom:-10px;
}
.marquee svg{
    padding-left: 100px;
}
@media (max-width : 700px){
    .contant .headdiv{
        flex-direction: column-reverse;
    }
    .contant .headdiv .item{
        width: 100%;
        height: 100vh;
    }
    .skills{
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: flex-start;
    }
    .skills .icons{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        width: 20% !important;
    }
    .skills .icons .skil{
        width: 60px;
        margin: 0;
    }
    .contant .headdiv .scrolldown{
        display: none;
    }
    .skills .item.content .chartbox h1{
        padding: 5px;
        text-wrap: nowrap;
        font-size: 22px;
    }
    .skills {
        flex-direction: column;
        height: fit-content;
        position: relative;
    }
    .skills .item{
        width: 100%;
    }
    .skills .item .chartbox {
        position: absolute;
        top: 0;
        left: 75px;
        background-color: #222;
        display: flex;
        height: 100vh !important;
        flex-direction: column;
        align-content: space-between;
        align-items: center;
        justify-content: center;
        width: 80% !important;
    }
    .skills .item.content{
        height: 0;
    }
    .workex{
        flex-direction: column;
    }
    .workex .workexitem{
        position: static;
        width: 100%;
        padding: 0;
    }
    .workex .workexitem.postionsticky{
        position: static;
        flex-wrap: nowrap;
        height: fit-content;
        padding: 0;
    }
    .workex .workexitem.postionsticky p{
        padding: 10px;
    }
    .workex .workexitem h1{
        text-align: center;
    }
    .skilschart{
        flex-direction: column-reverse;
    }
    .skilschart .item{
        width: 100%;
    }
    .skilschart .item h1{
        text-align: center;
    }
    .skilschart .cs50icon{
        width: 100%;
    }
    .skilschart .cs50icon img{
        width: 90%;
    }
    .footer {
        flex-direction: column-reverse;
        height: fit-content;
    }
    .footer .item .cvsbox{
        width: 1005;
    }
    .footer .item{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0;
        flex-direction: column-reverse;
    }
}